@import 'utils/styles/colors';
@import 'utils/styles/Typography.module.sass';

.logo {
    img {
        height: 80px; }
    p {
        @extend .TITLE_18;
        color: $MEDIUM_BLACK;
        margin: 12px 0; }

    span {
        @extend .BODY_12;
        display: inline-block;
        color: $DARK_GRAY;
        margin: {
            bottom: 24px; } } }

.login {
    position: relative;
    display: flex;
    height: 100%;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: auto {
 }        // bottom: 20px

    > span {
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%); } }

.content {
    width: 100%;
    max-width: 400px; }

.sub {
    display: flex;
    justify-content: space-between;
    margin: {
        bottom: 24px; }

    &Span {
        font: {
            size: 12px; }
        color: $MEDIUM_BLACK;

        a {
            display: inline-block;
            padding: {
                left: 5px; }
            font: {
                weight: 700; }
            text-decoration: underline; } } }

.buttons {
    display: flex;
    flex-direction: column;
    padding: 30px 60px;

    * {
        margin: {
            bottom: 12px; } } }

.loading {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(#fbfbfb, .9);
    z-index: 2; }

.button__facebook {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: transparent;
    border: none; }

@media only screen and (max-width: 768px) {

    .buttons {
        display: block;
        padding: 20px;
        * {
            display: block !important;
            min-width: unset !important;
            &:first-child {
                width: 100%; } } } }
