@import 'utils/styles/colors';

.card {
    // height: 100%
    display: flex;
    align-items: stretch; }

.circle {
    text-align: center;
    width: 35%; }

.content {
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
    margin: 6px;
    left: 12px; }

.locked {
    box-sizing: border-box;
    position: relative;
    height: 80px;
    width: 80px;
    // padding:
    //     bottom: 35%
    border-radius: 300px;
    border: 5px solid $MEDIUM_BLACK;

    img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        height: 30px;
        width: 30px; } }
