@import 'utils/styles/colors';

// Item

.item {
  width: 320px;
  margin: 32px;
  text-align: center; }

.image {
  padding: 32px;
  margin: {
    bottom: 12px; }
  background: $DARK_BLUE;
  border-radius: 5px;
  img {
    height: 40px; } }

// Infos

.infos {
  margin: 32px;
  padding: 32px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  background: #F7F7F7; }

@media only screen and (max-width: 768px) {
  .infos {
    margin: 12px;
    padding: 12px;
    display: grid;
    grid-template: auto / 1fr 1fr; }

  .item {
    width: auto;
    margin: 12px;
    * {
      font: {
        size: 16px !important; } } }

  .image {
    padding: 12px;
    margin: {
      bottom: 12px; }
    img {
      height: 30px; } } }
