
.card {
  position: relative;
  text-align: center;
  overflow: inherit; }

.image {
  position: absolute;
  text-align: center;
  left: 50%;
  top: -30px;
  transform: translateX(-50%);
  img:first-child {
    height: 60px;
    width: 60px;
    object-fit: cover;
    border-radius: 300px; } }

