@import 'utils/styles/colors';

.modal {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba($LIGHT_BLACK, .8);
  z-index: 100; }

.card {
  position: fixed;
  // max-width: 1000 - 14px
  margin: 0;
  max-width: 80%;
  width: 100%;
  top: 50%;
  left: 50%;
  max-height: 90vh;
  transform: translate(-50%, -50%);
  background: white;
  z-index: 99;
  overflow: auto;
  padding: 0; }

.title {
  // margin: -20px
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: {
    bottom: 20px; }
  padding: 20px;
  // background: $LIGHT_GRAY
  background: #F0F0F0; }

.close {
  padding: 12px;
  cursor: pointer; }

.content {
  position: relative;
  margin: 0;
  top: -24px;
  padding: 0; }

@media only screen and (max-width: 768px) {
  .card {
    max-width: 100%;
    max-height: 100vh; } }
