@import 'utils/styles/colors';

.wrapper {
  display: flex;
  padding: 36px;
  max-width: 100%;
  // align-items: flex-start
  justify-content: center;

  > * {
    flex: 1;
    &:nth-child(1) {
      flex: 2; } }

  &__info {
    display: flex;
    padding: 36px 64px; } }

.card {
  position: relative;
  z-index: 4;
  max-width: 620px;
  width: 100%;
  // min-width: 550px
  margin: {
    right: 36px; } }

.title {
  text-transform: uppercase;
  letter-spacing: 2px;
  &__modal {
    @extend .title;
    color: $MEDIUM_GRAY; }
  &__wrapper {
    display: flex;
    align-items: center; } }

.tag {
  display: inline-block;
  padding: 3px 12px;
  margin: 0 12px;
  border-radius: 300px;
  background: #F3F3F3;
  color: $DARK_GRAY;
  text-transform: capitalize; }

.content {
  position: relative;
  margin: {
    top: 12px; }
  z-index: 4;
  // width: 60%
  > * {
    font: {
      family: 'Open Sans' !important;
      weight: normal !important; }
 } }    // color: $MEDIUM_GRAY !important

.resume {
  &__list {
    margin: {
      top: 12px; }
    &__item {
      display: inline-block;
      list-style-type: none;
      width: 50%;
      text-transform: uppercase; } } }

.info {
  text-align: left;
  margin: {
    bottom: 60px; }

  &__list {
    list-style-type: none;
    padding: 6px 0;
    font: {
      weight: bold; }
    text-transform: uppercase; } }

@media only screen and (max-width: 768px) {

  .wrapper {
    padding: 12px;
    display: block; }

  .card {
    width: auto;
    margin: {
      right: 0; } }

  .resume {
    &__list {
      &__item {
        width: 100%; } } } }
